'use client';

import React, { useState, useEffect } from 'react';
import {
  Box,
  Paper,
  Typography,
  TextField,
  Button,
  Alert,
  Container,
  InputAdornment,
  IconButton,
  Stepper,
  Step,
  StepLabel
} from '@mui/material';
import { Visibility, VisibilityOff, Refresh, ArrowBack } from '@mui/icons-material';

interface ForgotPasswordProps {
  onBackToLogin: () => void;
}

const ForgotPassword: React.FC<ForgotPasswordProps> = ({ onBackToLogin }) => {
  const [activeStep, setActiveStep] = useState(0);
  const [formData, setFormData] = useState({
    employeeId: '',
    otp: '',
    captcha: '',
    newPassword: '',
    confirmPassword: ''
  });
  const [errors, setErrors] = useState<Record<string, string>>({});
  const [captchaText, setCaptchaText] = useState('Captcha23');
  const [showNewPassword, setShowNewPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [otpSent, setOtpSent] = useState(false);
  const [otpExpiry, setOtpExpiry] = useState<Date | null>(null);
  const [canResendOtp, setCanResendOtp] = useState(true);
  const [resendTimer, setResendTimer] = useState(0);

  const steps = ['Verify Employee ID', 'Verify OTP', 'Set New Password'];

  // Timer for OTP resend
  useEffect(() => {
    let timer: NodeJS.Timeout;
    if (resendTimer > 0) {
      timer = setTimeout(() => setResendTimer(resendTimer - 1), 1000);
    } else if (resendTimer === 0 && !canResendOtp) {
      setCanResendOtp(true);
    }
    return () => clearTimeout(timer);
  }, [resendTimer, canResendOtp]);

  // Check OTP expiry
  useEffect(() => {
    if (otpExpiry) {
      const checkExpiry = () => {
        if (new Date() > otpExpiry) {
          setErrors({ otp: 'OTP has expired. Please generate a new OTP.' });
          setOtpSent(false);
        }
      };
      
      const interval = setInterval(checkExpiry, 1000);
      return () => clearInterval(interval);
    }
  }, [otpExpiry]);

  const handleInputChange = (field: string) => (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    
    // Employee ID validation - only allow 6 digits
    if (field === 'employeeId') {
      if (value.length > 6 || !/^\d*$/.test(value)) {
        return;
      }
    }
    
    // Password length validation
    if ((field === 'newPassword' || field === 'confirmPassword') && value.length > 12) {
      return;
    }

    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
    
    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({
        ...prev,
        [field]: ''
      }));
    }
  };

  const validateEmployeeId = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.employeeId) {
      newErrors.employeeId = 'Employee ID is mandatory';
    } else if (formData.employeeId.length !== 6) {
      newErrors.employeeId = 'Employee Id should be 6 digits';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const validateOtp = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.otp) {
      newErrors.otp = 'OTP is mandatory';
    }

    if (!formData.captcha) {
      newErrors.captcha = 'CAPTCHA is mandatory';
    } else if (formData.captcha !== captchaText) {
      newErrors.captcha = 'Incorrect CAPTCHA, please re-enter';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const validatePassword = () => {
    const newErrors: Record<string, string> = {};
    const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]/;

    if (!formData.newPassword) {
      newErrors.newPassword = 'New password is mandatory';
    } else if (formData.newPassword.length < 8 || formData.newPassword.length > 12) {
      newErrors.newPassword = 'Password should be between 8 and 12 characters';
    } else if (!passwordRegex.test(formData.newPassword)) {
      newErrors.newPassword = 'Password must contain at least 1 capital letter, 1 small letter, 1 special character and 1 number';
    }

    if (!formData.confirmPassword) {
      newErrors.confirmPassword = 'Confirm password is mandatory';
    } else if (formData.newPassword !== formData.confirmPassword) {
      newErrors.confirmPassword = 'New Password and Confirm New Password does not match';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleGenerateOtp = async () => {
    if (!validateEmployeeId()) {
      return;
    }

    // Simulate employee ID validation
    const isValidEmployee = formData.employeeId === '123456'; // Mock validation

    if (!isValidEmployee) {
      setErrors({ employeeId: 'Entered Employee Id does not exist or ID is not active' });
      return;
    }

    // Generate OTP
    setOtpSent(true);
    setCanResendOtp(false);
    setResendTimer(30);
    
    // Set OTP expiry to 11:59:59 PM of current date
    const expiry = new Date();
    expiry.setHours(23, 59, 59, 999);
    setOtpExpiry(expiry);
    
    alert('OTP sent to your registered mobile number');
  };

  const handleVerifyOtp = async () => {
    if (!validateOtp()) {
      return;
    }

    // Simulate OTP validation
    const isValidOtp = formData.otp === '123456'; // Mock validation

    if (!isValidOtp) {
      setErrors({ otp: 'Incorrect OTP' });
      return;
    }

    // Disable employee ID and OTP fields, enable password fields
    setActiveStep(2);
  };

  const handleContinue = () => {
    if (activeStep === 0 && otpSent && validateOtp()) {
      handleVerifyOtp();
    }
  };

  const handleSetNewPassword = async () => {
    if (!validatePassword()) {
      return;
    }

    try {
      // Simulate password update
      console.log('Password updated successfully');
      alert('Password updated successfully! Redirecting to login page...');
      
      // Redirect to login page
      setTimeout(() => {
        onBackToLogin();
      }, 2000);
    } catch (error) {
      setErrors({ general: 'Failed to update password. Please try again.' });
    }
  };

  const generateNewCaptcha = () => {
    const captchas = ['Captcha23', 'Secure45', 'Verify67', 'Check89', 'Guard12'];
    const newCaptcha = captchas[Math.floor(Math.random() * captchas.length)];
    setCaptchaText(newCaptcha);
    setFormData(prev => ({ ...prev, captcha: '' }));
  };

  const handleCancel = () => {
    onBackToLogin();
  };

  return (
    <Box sx={{ 
      minHeight: '100vh', 
      background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      position: 'relative',
      '&::before': {
        content: '""',
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        background: 'rgba(0,0,0,0.1)',
        zIndex: 1
      }
    }}>
      <Container maxWidth="lg" sx={{ position: 'relative', zIndex: 2, py: 2 }}>
        {/* Header */}
        <Paper elevation={8} sx={{ 
          background: 'linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%)',
          color: 'white', 
          p: 3, 
          mb: 2,
          borderRadius: 3,
          boxShadow: '0 8px 32px rgba(0,0,0,0.3)',
          border: '1px solid rgba(255,255,255,0.1)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between'
        }}>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 3 }}>
            <Box sx={{
              width: 60,
              height: 60,
              borderRadius: '50%',
              background: 'linear-gradient(135deg, #ffd700 0%, #ffed4e 100%)',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              boxShadow: '0 4px 16px rgba(255,215,0,0.4)',
              fontSize: '24px',
              fontWeight: 'bold',
              color: '#1e3a8a'
            }}>
              üèõÔ∏è
            </Box>
            <Box>
              <Typography variant="h4" fontWeight="700" sx={{ 
                textShadow: '2px 2px 4px rgba(0,0,0,0.3)',
                letterSpacing: '0.5px'
              }}>
                Directorate of Municipal Administration
              </Typography>
              <Typography variant="h6" sx={{ 
                opacity: 0.9,
                fontWeight: 300,
                letterSpacing: '1px'
              }}>
                GOVERNMENT OF KARNATAKA
              </Typography>
            </Box>
          </Box>
        </Paper>

        {/* Navigation Bar */}
        <Paper elevation={4} sx={{ 
          background: 'linear-gradient(135deg, #1e40af 0%, #1e3a8a 100%)', 
          color: 'white', 
          p: 2, 
          mb: 3,
          borderRadius: 2,
          boxShadow: '0 4px 16px rgba(0,0,0,0.2)',
          display: 'flex', 
          alignItems: 'center', 
          gap: 2 
        }}>
          <IconButton 
            onClick={onBackToLogin} 
            sx={{ 
              color: 'white',
              background: 'rgba(255,255,255,0.1)',
              borderRadius: 2,
              '&:hover': {
                background: 'rgba(255,255,255,0.2)',
                transform: 'translateX(-2px)'
              },
              transition: 'all 0.3s ease'
            }}
          >
            <ArrowBack />
          </IconButton>
          <Typography variant="h5" sx={{ fontWeight: 500, letterSpacing: '0.5px' }}>üè† Home</Typography>
        </Paper>

        {/* Forgot Password Header */}
        <Paper elevation={6} sx={{ 
          background: 'linear-gradient(135deg, #dc2626 0%, #ef4444 100%)', 
          color: 'white', 
          p: 3, 
          mb: 3, 
          textAlign: 'center',
          borderRadius: 3,
          boxShadow: '0 6px 24px rgba(220,38,38,0.3)'
        }}>
          <Typography variant="h4" fontWeight="700" sx={{
            textShadow: '2px 2px 4px rgba(0,0,0,0.3)',
            letterSpacing: '0.5px'
          }}>
            üîë Forgot Password
          </Typography>
        </Paper>

        {/* Progress Stepper */}
        <Paper elevation={4} sx={{ 
          p: 4, 
          mb: 3,
          borderRadius: 3,
          background: 'rgba(255,255,255,0.95)',
          backdropFilter: 'blur(20px)',
          border: '1px solid rgba(255,255,255,0.3)'
        }}>
          <Stepper 
            activeStep={activeStep} 
            alternativeLabel
            sx={{
              '& .MuiStepIcon-root': {
                fontSize: '2rem',
                '&.Mui-active': {
                  color: '#1e3a8a'
                },
                '&.Mui-completed': {
                  color: '#059669'
                }
              },
              '& .MuiStepLabel-label': {
                fontWeight: 600,
                fontSize: '16px',
                '&.Mui-active': {
                  color: '#1e3a8a'
                },
                '&.Mui-completed': {
                  color: '#059669'
                }
              }
            }}
          >
            {steps.map((label, index) => (
              <Step key={label}>
                <StepLabel>
                  {index === 0 && 'üë§ '}
                  {index === 1 && 'üì± '}
                  {index === 2 && 'üîí '}
                  {label}
                </StepLabel>
              </Step>
            ))}
          </Stepper>
        </Paper>

        {/* Form */}
        <Box sx={{ display: 'flex', justifyContent: 'center' }}>
          <Paper elevation={12} sx={{ 
            width: '100%', 
            maxWidth: 520, 
            p: 4,
            borderRadius: 4,
            background: 'rgba(255,255,255,0.95)',
            backdropFilter: 'blur(20px)',
            border: '1px solid rgba(255,255,255,0.3)',
            boxShadow: '0 16px 40px rgba(0,0,0,0.2)'
          }}>
          {errors.general && (
            <Alert severity="error" sx={{ mb: 2 }}>
              {errors.general}
            </Alert>
          )}

            {/* Step 1: Employee ID */}
            {activeStep === 0 && (
              <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
                <TextField
                  label="üë§ User ID"
                  value={formData.employeeId}
                  onChange={handleInputChange('employeeId')}
                  error={!!errors.employeeId}
                  helperText={errors.employeeId}
                  fullWidth
                  inputProps={{ maxLength: 6 }}
                  disabled={otpSent}
                  sx={{
                    '& .MuiOutlinedInput-root': {
                      borderRadius: 3,
                      background: otpSent ? 'rgba(0,0,0,0.05)' : 'rgba(255,255,255,0.8)',
                      '&:hover fieldset': {
                        borderColor: '#3b82f6',
                        borderWidth: 2
                      },
                      '&.Mui-focused fieldset': {
                        borderColor: '#1e3a8a',
                        borderWidth: 2
                      }
                    },
                    '& .MuiInputLabel-root': {
                      fontWeight: 500
                    }
                  }}
                />

                <Button
                  variant="contained"
                  onClick={handleGenerateOtp}
                  disabled={!canResendOtp || otpSent}
                  sx={{ 
                    background: (!canResendOtp || otpSent) ? '#64748b' : 'linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%)',
                    borderRadius: 3,
                    py: 1.5,
                    fontSize: '16px',
                    fontWeight: 600,
                    textTransform: 'none',
                    boxShadow: (!canResendOtp || otpSent) ? 'none' : '0 4px 16px rgba(30,58,138,0.3)',
                    '&:hover': { 
                      background: (!canResendOtp || otpSent) ? '#64748b' : 'linear-gradient(135deg, #1e40af 0%, #2563eb 100%)',
                      transform: (!canResendOtp || otpSent) ? 'none' : 'translateY(-2px)',
                      boxShadow: (!canResendOtp || otpSent) ? 'none' : '0 6px 20px rgba(30,58,138,0.4)'
                    },
                    transition: 'all 0.3s ease'
                  }}
                >
                  {otpSent ? '‚úÖ OTP Sent' : 'üì± Generate OTP'}
                  {!canResendOtp && ` (${resendTimer}s)`}
                </Button>

                {otpSent && (
                  <>
                    <TextField
                      label="üì± OTP"
                      value={formData.otp}
                      onChange={handleInputChange('otp')}
                      error={!!errors.otp}
                      helperText={errors.otp}
                      fullWidth
                      disabled={activeStep > 0}
                      sx={{
                        '& .MuiOutlinedInput-root': {
                          borderRadius: 3,
                          background: activeStep > 0 ? 'rgba(0,0,0,0.05)' : 'rgba(255,255,255,0.8)',
                          '&:hover fieldset': {
                            borderColor: '#3b82f6',
                            borderWidth: 2
                          },
                          '&.Mui-focused fieldset': {
                            borderColor: '#1e3a8a',
                            borderWidth: 2
                          }
                        },
                        '& .MuiInputLabel-root': {
                          fontWeight: 500
                        }
                      }}
                    />

                    <Box sx={{ display: 'flex', alignItems: 'flex-start', gap: 2 }}>
                      <TextField
                        label="üî§ CAPTCHA"
                        value={formData.captcha}
                        onChange={handleInputChange('captcha')}
                        error={!!errors.captcha}
                        helperText={errors.captcha}
                        fullWidth
                        disabled={activeStep > 0}
                        sx={{
                          '& .MuiOutlinedInput-root': {
                            borderRadius: 3,
                            background: activeStep > 0 ? 'rgba(0,0,0,0.05)' : 'rgba(255,255,255,0.8)',
                            '&:hover fieldset': {
                              borderColor: '#3b82f6',
                              borderWidth: 2
                            },
                            '&.Mui-focused fieldset': {
                              borderColor: '#1e3a8a',
                              borderWidth: 2
                            }
                          },
                          '& .MuiInputLabel-root': {
                            fontWeight: 500
                          }
                        }}
                      />
                      <Paper elevation={3} sx={{ 
                        border: '2px solid #e5e7eb', 
                        p: 2, 
                        background: 'linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%)', 
                        minWidth: '120px',
                        height: '56px',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        fontFamily: 'monospace',
                        fontSize: '20px',
                        fontWeight: 'bold',
                        color: '#1e3a8a',
                        borderRadius: 3,
                        letterSpacing: '2px'
                      }}>
                        {captchaText}
                      </Paper>
                      <IconButton 
                        onClick={generateNewCaptcha} 
                        title="Generate new CAPTCHA"
                        disabled={activeStep > 0}
                        sx={{
                          mt: 1,
                          color: '#1e3a8a',
                          background: 'rgba(30,58,138,0.1)',
                          borderRadius: 2,
                          '&:hover': {
                            background: 'rgba(30,58,138,0.2)',
                            transform: 'rotate(180deg)'
                          },
                          '&.Mui-disabled': {
                            color: '#64748b',
                            background: 'rgba(100,116,139,0.1)'
                          },
                          transition: 'all 0.3s ease'
                        }}
                      >
                        <Refresh />
                      </IconButton>
                    </Box>

                    <Button
                      variant="contained"
                      onClick={handleContinue}
                      sx={{ 
                        background: 'linear-gradient(135deg, #059669 0%, #10b981 100%)',
                        borderRadius: 3,
                        py: 1.5,
                        fontSize: '16px',
                        fontWeight: 600,
                        textTransform: 'none',
                        boxShadow: '0 4px 16px rgba(5,150,105,0.3)',
                        '&:hover': { 
                          background: 'linear-gradient(135deg, #047857 0%, #059669 100%)',
                          transform: 'translateY(-2px)',
                          boxShadow: '0 6px 20px rgba(5,150,105,0.4)'
                        },
                        transition: 'all 0.3s ease'
                      }}
                    >
                      ‚û°Ô∏è Continue
                    </Button>
                </>
              )}

                {canResendOtp && otpSent && (
                  <Button
                    variant="outlined"
                    onClick={handleGenerateOtp}
                    sx={{ 
                      borderColor: '#3b82f6', 
                      color: '#3b82f6',
                      borderWidth: 2,
                      borderRadius: 3,
                      py: 1.5,
                      fontSize: '16px',
                      fontWeight: 600,
                      textTransform: 'none',
                      '&:hover': {
                        borderColor: '#1e3a8a',
                        color: '#1e3a8a',
                        background: 'rgba(30,58,138,0.1)',
                        transform: 'translateY(-2px)'
                      },
                      transition: 'all 0.3s ease'
                    }}
                  >
                    üîÑ Resend OTP
                  </Button>
                )}
            </Box>
          )}

            {/* Step 2: Set New Password */}
            {activeStep === 2 && (
              <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
                <Alert 
                  severity="success" 
                  sx={{ 
                    mb: 2,
                    borderRadius: 3,
                    '& .MuiAlert-icon': {
                      fontSize: '24px'
                    }
                  }}
                >
                  ‚úÖ OTP verified successfully. Please set your new password.
                </Alert>

                <TextField
                  label="üîí New Password"
                  type={showNewPassword ? 'text' : 'password'}
                  value={formData.newPassword}
                  onChange={handleInputChange('newPassword')}
                  error={!!errors.newPassword}
                  helperText={errors.newPassword}
                  fullWidth
                  inputProps={{ maxLength: 12, minLength: 8 }}
                  sx={{
                    '& .MuiOutlinedInput-root': {
                      borderRadius: 3,
                      background: 'rgba(255,255,255,0.8)',
                      '&:hover fieldset': {
                        borderColor: '#3b82f6',
                        borderWidth: 2
                      },
                      '&.Mui-focused fieldset': {
                        borderColor: '#1e3a8a',
                        borderWidth: 2
                      }
                    },
                    '& .MuiInputLabel-root': {
                      fontWeight: 500
                    }
                  }}
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          onClick={() => setShowNewPassword(!showNewPassword)}
                          edge="end"
                          sx={{
                            color: '#1e3a8a',
                            '&:hover': {
                              background: 'rgba(30,58,138,0.1)'
                            }
                          }}
                        >
                          {showNewPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    )
                  }}
                />

                <TextField
                  label="üîì Confirm New Password"
                  type={showConfirmPassword ? 'text' : 'password'}
                  value={formData.confirmPassword}
                  onChange={handleInputChange('confirmPassword')}
                  error={!!errors.confirmPassword}
                  helperText={errors.confirmPassword}
                  fullWidth
                  inputProps={{ maxLength: 12, minLength: 8 }}
                  sx={{
                    '& .MuiOutlinedInput-root': {
                      borderRadius: 3,
                      background: 'rgba(255,255,255,0.8)',
                      '&:hover fieldset': {
                        borderColor: '#3b82f6',
                        borderWidth: 2
                      },
                      '&.Mui-focused fieldset': {
                        borderColor: '#1e3a8a',
                        borderWidth: 2
                      }
                    },
                    '& .MuiInputLabel-root': {
                      fontWeight: 500
                    }
                  }}
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                          edge="end"
                          sx={{
                            color: '#1e3a8a',
                            '&:hover': {
                              background: 'rgba(30,58,138,0.1)'
                            }
                          }}
                        >
                          {showConfirmPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    )
                  }}
                />

                <Box sx={{ display: 'flex', gap: 3 }}>
                  <Button
                    variant="outlined"
                    onClick={handleCancel}
                    sx={{ 
                      flex: 1, 
                      borderColor: '#dc2626', 
                      color: '#dc2626',
                      borderWidth: 2,
                      borderRadius: 3,
                      py: 1.5,
                      fontSize: '16px',
                      fontWeight: 600,
                      textTransform: 'none',
                      '&:hover': {
                        borderColor: '#b91c1c',
                        color: '#b91c1c',
                        background: 'rgba(220,38,38,0.1)',
                        transform: 'translateY(-2px)'
                      },
                      transition: 'all 0.3s ease'
                    }}
                  >
                    ‚ùå Cancel
                  </Button>
                  <Button
                    variant="contained"
                    onClick={handleSetNewPassword}
                    sx={{ 
                      flex: 1,
                      background: 'linear-gradient(135deg, #059669 0%, #10b981 100%)',
                      borderRadius: 3,
                      py: 1.5,
                      fontSize: '16px',
                      fontWeight: 600,
                      textTransform: 'none',
                      boxShadow: '0 4px 16px rgba(5,150,105,0.3)',
                      '&:hover': { 
                        background: 'linear-gradient(135deg, #047857 0%, #059669 100%)',
                        transform: 'translateY(-2px)',
                        boxShadow: '0 6px 20px rgba(5,150,105,0.4)'
                      },
                      transition: 'all 0.3s ease'
                    }}
                  >
                    ‚úÖ Set New Password
                  </Button>
                </Box>
              </Box>
            )}
          </Paper>
        </Box>
      </Container>
    </Box>
  );
};

export default ForgotPassword;