'use client';

import React, { useState } from 'react';
import {
  Box,
  Paper,
  Typography,
  TextField,
  Button,
  Tabs,
  Tab,
  Alert,
  Container,
  InputAdornment,
  IconButton,
  Card,
  CardContent,
  Fade,
  Slide,
  Chip,
  LinearProgress,
  Tooltip
} from '@mui/material';
import {
  Visibility,
  VisibilityOff,
  Refresh,
  Security,
  Phone,
  Person,
  VpnKey,
  Lock,
  CheckCircle,
  Error as ErrorIcon
} from '@mui/icons-material';
import ForgotPassword from './ForgotPassword';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`login-tabpanel-${index}`}
      aria-labelledby={`login-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

const LoginPage: React.FC = () => {
  const [tabValue, setTabValue] = useState(1);
  const [showPassword, setShowPassword] = useState(false);
  const [showForgotPassword, setShowForgotPassword] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [formData, setFormData] = useState({
    employeeId: '',
    password: '',
    otp: '',
    captcha: ''
  });
  const [errors, setErrors] = useState<Record<string, string>>({});
  const [captchaText, setCaptchaText] = useState('Captcha23');
  const [attemptCount, setAttemptCount] = useState(0);
  const [isLocked, setIsLocked] = useState(false);
  const [otpGenerated, setOtpGenerated] = useState(false);
  const [showSuccessAnimation, setShowSuccessAnimation] = useState(false);

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
    setShowForgotPassword(false);
    clearForm();
  };

  const handleInputChange = (field: string) => (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;

    if (field === 'employeeId') {
      if (value.length > 6 || !/^\d*$/.test(value)) {
        return;
      }
    }

    if (field === 'password') {
      if (value.length > 12) {
        return;
      }
    }

    setFormData(prev => ({
      ...prev,
      [field]: value
    }));

    if (errors[field]) {
      setErrors(prev => ({
        ...prev,
        [field]: ''
      }));
    }
  };

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.employeeId) {
      newErrors.employeeId = 'Employee ID is mandatory';
    } else if (formData.employeeId.length !== 6) {
      newErrors.employeeId = 'Employee Id should be 6 digits';
    }

    if (!formData.password) {
      newErrors.password = 'Password is mandatory';
    } else if (formData.password.length < 8 || formData.password.length > 12) {
      newErrors.password = 'Password should be between 8 and 12 characters';
    }

    if (!formData.otp) {
      newErrors.otp = 'OTP is mandatory';
    }

    if (!formData.captcha) {
      newErrors.captcha = 'CAPTCHA is mandatory';
    } else if (formData.captcha !== captchaText) {
      newErrors.captcha = 'Incorrect CAPTCHA, please re-enter';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleLogin = async () => {
    if (isLocked) {
      setErrors({ general: 'User ID is locked. Change password using forgot password link' });
      return;
    }

    if (!validateForm()) {
      return;
    }

    setIsLoading(true);

    await new Promise(resolve => setTimeout(resolve, 2000));

    const isValidEmployee = formData.employeeId === '123456';
    const isValidPassword = formData.password === 'password123';

    if (!isValidEmployee) {
      setErrors({ general: 'Please Enter valid employee id' });
      setIsLoading(false);
      return;
    }

    if (!isValidPassword) {
      const newAttemptCount = attemptCount + 1;
      setAttemptCount(newAttemptCount);

      if (newAttemptCount >= 5) {
        setIsLocked(true);
        setErrors({ general: 'User ID is locked. Change password using forgot password link' });
      } else {
        setErrors({
          general: `Please enter correct password, left with ${5 - newAttemptCount} attempts`
        });
      }
      clearForm();
      setIsLoading(false);
      return;
    }

    setAttemptCount(0);
    setShowSuccessAnimation(true);

    setTimeout(() => {
      console.log('Login successful');
      alert('Login successful!');
      setIsLoading(false);
      setShowSuccessAnimation(false);
    }, 1500);
  };

  const generateOTP = async () => {
    if (!formData.employeeId) {
      setErrors({ employeeId: 'Please enter Employee ID first' });
      return;
    }

    setIsLoading(true);
    await new Promise(resolve => setTimeout(resolve, 1000));
    setOtpGenerated(true);
    setIsLoading(false);
    alert('OTP sent to your registered mobile number');
  };

  const generateNewCaptcha = () => {
    const captchas = ['Captcha23', 'Secure45', 'Verify67', 'Check89', 'Guard12'];
    const newCaptcha = captchas[Math.floor(Math.random() * captchas.length)];
    setCaptchaText(newCaptcha);
    setFormData(prev => ({ ...prev, captcha: '' }));
  };

  const clearForm = () => {
    setFormData({
      employeeId: '',
      password: '',
      otp: '',
      captcha: ''
    });
    setErrors({});
    setOtpGenerated(false);
  };

  const handleForgotPasswordClick = () => {
    setShowForgotPassword(true);
  };

  const handleBackToLogin = () => {
    setShowForgotPassword(false);
    clearForm();
  };

  if (showForgotPassword) {
    return <ForgotPassword onBackToLogin={handleBackToLogin} />;
  }

  return (
    <Box sx={{
      minHeight: '100vh',
      background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      position: 'relative',
      overflow: 'hidden',
      '&::before': {
        content: '""',
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        background: 'rgba(0,0,0,0.1)',
        zIndex: 1
      },
      '&::after': {
        content: '""',
        position: 'absolute',
        top: '20%',
        right: '-10%',
        width: '400px',
        height: '400px',
        background: 'radial-gradient(circle, rgba(255,255,255,0.1) 0%, transparent 70%)',
        borderRadius: '50%',
        zIndex: 1
      }
    }}>
      <Container maxWidth="lg" sx={{ position: 'relative', zIndex: 2, py: 2 }}>
        <Fade in timeout={1000}>
          <Paper elevation={12} sx={{
            background: 'linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%)',
            color: 'white',
            p: 4,
            mb: 3,
            borderRadius: 4,
            boxShadow: '0 12px 48px rgba(0,0,0,0.4)',
            border: '1px solid rgba(255,255,255,0.2)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            position: 'relative',
            overflow: 'hidden',
            '&::before': {
              content: '""',
              position: 'absolute',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              background: 'url("data:image/svg+xml,%3Csvg width="60" height="60" viewBox="0 0 60 60" xmlns="http://www.w3.org/2000/svg"%3E%3Cg fill="none" fill-rule="evenodd"%3E%3Cg fill="%23ffffff" fill-opacity="0.05"%3E%3Ccircle cx="30" cy="30" r="1"/%3E%3C/g%3E%3C/g%3E%3C/svg%3E")',
              zIndex: 0
            }
          }}>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 3, zIndex: 1 }}>
              <Box sx={{
                width: 70,
                height: 70,
                borderRadius: '50%',
                background: 'linear-gradient(135deg, #ffd700 0%, #ffed4e 100%)',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                boxShadow: '0 8px 24px rgba(255,215,0,0.5)',
                fontSize: '28px',
                fontWeight: 'bold',
                color: '#1e3a8a',
                border: '3px solid rgba(255,255,255,0.3)'
              }}>
                üèõÔ∏è
              </Box>
              <Box>
                <Typography variant="h3" fontWeight="800" sx={{
                  textShadow: '3px 3px 6px rgba(0,0,0,0.4)',
                  letterSpacing: '0.5px',
                  background: 'linear-gradient(135deg, #ffffff 0%, #e2e8f0 100%)',
                  backgroundClip: 'text',
                  WebkitBackgroundClip: 'text',
                  WebkitTextFillColor: 'transparent'
                }}>
                  Directorate of Municipal Administration
                </Typography>
                <Typography variant="h6" sx={{
                  opacity: 0.9,
                  fontWeight: 400,
                  letterSpacing: '2px',
                  textShadow: '1px 1px 2px rgba(0,0,0,0.3)'
                }}>
                  GOVERNMENT OF KARNATAKA
                </Typography>
                <Chip
                  label="ERP System v2.0"
                  size="small"
                  sx={{
                    mt: 1,
                    background: 'rgba(255,255,255,0.2)',
                    color: 'white',
                    fontWeight: 600
                  }}
                />
              </Box>
            </Box>
            <Button
              variant="contained"
              sx={{
                background: 'linear-gradient(135deg, rgba(255,255,255,0.25) 0%, rgba(255,255,255,0.15) 100%)',
                backdropFilter: 'blur(20px)',
                border: '2px solid rgba(255,255,255,0.4)',
                borderRadius: 4,
                px: 6,
                py: 2,
                fontSize: '18px',
                fontWeight: 700,
                textTransform: 'none',
                color: 'white',
                zIndex: 1,
                '&:hover': {
                  background: 'linear-gradient(135deg, rgba(255,255,255,0.35) 0%, rgba(255,255,255,0.25) 100%)',
                  transform: 'translateY(-3px)',
                  boxShadow: '0 12px 32px rgba(0,0,0,0.3)'
                },
                transition: 'all 0.4s ease'
              }}
            >
              üöÄ Portal ‚Üí
            </Button>
          </Paper>
        </Fade>

        <Slide direction="down" in timeout={1200}>
          <Paper elevation={6} sx={{
            background: 'linear-gradient(135deg, #1e40af 0%, #1e3a8a 100%)',
            color: 'white',
            p: 3,
            mb: 4,
            borderRadius: 3,
            boxShadow: '0 8px 24px rgba(0,0,0,0.3)',
            position: 'relative',
            overflow: 'hidden'
          }}>
            <Typography variant="h4" sx={{
              fontWeight: 600,
              letterSpacing: '0.5px',
              display: 'flex',
              alignItems: 'center',
              gap: 2
            }}>
              üè† Dashboard Home
            </Typography>
          </Paper>
        </Slide>

        <Fade in timeout={1500}>
          <Box sx={{ display: 'flex', justifyContent: 'center' }}>
            <Card elevation={16} sx={{
              width: '100%',
              maxWidth: 600,
              borderRadius: 5,
              background: 'rgba(255,255,255,0.98)',
              backdropFilter: 'blur(30px)',
              border: '2px solid rgba(255,255,255,0.5)',
              boxShadow: '0 24px 64px rgba(0,0,0,0.25)',
              position: 'relative',
              overflow: 'hidden',
              '&::before': {
                content: '""',
                position: 'absolute',
                top: 0,
                left: 0,
                right: 0,
                height: '6px',
                background: 'linear-gradient(90deg, #1e3a8a, #3b82f6, #06d6a0, #ffd60a)',
                zIndex: 1
              }
            }}>
              <CardContent sx={{ p: 5 }}>
                <Box sx={{ textAlign: 'center', mb: 4 }}>
                  <Security sx={{ fontSize: 60, color: '#1e3a8a', mb: 2 }} />
                  <Typography variant="h3" sx={{
                    fontWeight: 800,
                    background: 'linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%)',
                    backgroundClip: 'text',
                    WebkitBackgroundClip: 'text',
                    WebkitTextFillColor: 'transparent',
                    mb: 1
                  }}>
                    Secure Login
                  </Typography>
                  <Typography variant="body1" color="text.secondary" sx={{ fontWeight: 500 }}>
                    Access your municipal administration portal
                  </Typography>
                </Box>

                {isLoading && (
                  <LinearProgress
                    sx={{
                      mb: 3,
                      borderRadius: 2,
                      height: 6,
                      background: 'rgba(30,58,138,0.1)',
                      '& .MuiLinearProgress-bar': {
                        background: 'linear-gradient(90deg, #1e3a8a, #3b82f6)'
                      }
                    }}
                  />
                )}

                <Tabs
                  value={tabValue}
                  onChange={handleTabChange}
                  centered
                  sx={{
                    mb: 4,
                    '& .MuiTabs-indicator': {
                      background: 'linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%)',
                      height: 4,
                      borderRadius: 2
                    }
                  }}
                >
                  <Tab
                    icon={<Person />}
                    label="Citizen Portal"
                    sx={{
                      fontWeight: 700,
                      fontSize: '16px',
                      textTransform: 'none',
                      borderRadius: 3,
                      mx: 1,
                      py: 2,
                      minHeight: 64,
                      '&.Mui-selected': {
                        background: 'linear-gradient(135deg, rgba(30,58,138,0.15) 0%, rgba(59,130,246,0.15) 100%)',
                        color: '#1e3a8a'
                      }
                    }}
                  />
                  <Tab
                    icon={<VpnKey />}
                    label="Employee Access"
                    sx={{
                      fontWeight: 700,
                      fontSize: '16px',
                      textTransform: 'none',
                      borderRadius: 3,
                      mx: 1,
                      py: 2,
                      minHeight: 64,
                      '&.Mui-selected': {
                        background: 'linear-gradient(135deg, rgba(30,58,138,0.15) 0%, rgba(59,130,246,0.15) 100%)',
                        color: '#1e3a8a'
                      }
                    }}
                  />
                </Tabs>

                <TabPanel value={tabValue} index={0}>
                  <Box sx={{ textAlign: 'center', py: 6 }}>
                    <Person sx={{ fontSize: 80, color: '#64748b', mb: 2 }} />
                    <Typography variant="h5" color="text.secondary" sx={{ fontWeight: 600 }}>
                      Citizen Portal
                    </Typography>
                    <Typography variant="body1" color="text.secondary" sx={{ mt: 1 }}>
                      Coming Soon - Access municipal services as a citizen
                    </Typography>
                  </Box>
                </TabPanel>

                <TabPanel value={tabValue} index={1}>
                  {errors.general && (
                    <Alert
                      severity="error"
                      icon={<ErrorIcon />}
                      sx={{
                        mb: 3,
                        borderRadius: 3,
                        '& .MuiAlert-icon': {
                          fontSize: '28px'
                        }
                      }}
                    >
                      {errors.general}
                    </Alert>
                  )}

                  {showSuccessAnimation && (
                    <Alert
                      severity="success"
                      icon={<CheckCircle />}
                      sx={{
                        mb: 3,
                        borderRadius: 3,
                        '& .MuiAlert-icon': {
                          fontSize: '28px'
                        }
                      }}
                    >
                      Login successful! Redirecting to dashboard...
                    </Alert>
                  )}

                  <Box component="form" sx={{ display: 'flex', flexDirection: 'column', gap: 4 }}>
                    <TextField
                      label="Employee ID"
                      value={formData.employeeId}
                      onChange={handleInputChange('employeeId')}
                      error={!!errors.employeeId}
                      helperText={errors.employeeId}
                      fullWidth
                      inputProps={{ maxLength: 6 }}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <Person sx={{ color: '#1e3a8a' }} />
                          </InputAdornment>
                        )
                      }}
                      sx={{
                        '& .MuiOutlinedInput-root': {
                          borderRadius: 3,
                          background: 'rgba(255,255,255,0.9)',
                          '&:hover fieldset': {
                            borderColor: '#3b82f6',
                            borderWidth: 2
                          },
                          '&.Mui-focused fieldset': {
                            borderColor: '#1e3a8a',
                            borderWidth: 2
                          }
                        },
                        '& .MuiInputLabel-root': {
                          fontWeight: 600
                        }
                      }}
                    />

                    <TextField
                      label="Password"
                      type={showPassword ? 'text' : 'password'}
                      value={formData.password}
                      onChange={handleInputChange('password')}
                      error={!!errors.password}
                      helperText={errors.password}
                      fullWidth
                      inputProps={{ maxLength: 12, minLength: 8 }}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <Lock sx={{ color: '#1e3a8a' }} />
                          </InputAdornment>
                        ),
                        endAdornment: (
                          <InputAdornment position="end">
                            <Tooltip title={showPassword ? "Hide password" : "Show password"}>
                              <IconButton
                                onClick={() => setShowPassword(!showPassword)}
                                edge="end"
                                sx={{
                                  color: '#1e3a8a',
                                  '&:hover': {
                                    background: 'rgba(30,58,138,0.1)'
                                  }
                                }}
                              >
                                {showPassword ? <VisibilityOff /> : <Visibility />}
                              </IconButton>
                            </Tooltip>
                          </InputAdornment>
                        )
                      }}
                      sx={{
                        '& .MuiOutlinedInput-root': {
                          borderRadius: 3,
                          background: 'rgba(255,255,255,0.9)',
                          '&:hover fieldset': {
                            borderColor: '#3b82f6',
                            borderWidth: 2
                          },
                          '&.Mui-focused fieldset': {
                            borderColor: '#1e3a8a',
                            borderWidth: 2
                          }
                        },
                        '& .MuiInputLabel-root': {
                          fontWeight: 600
                        }
                      }}
                    />

                    <Button
                      variant="contained"
                      onClick={generateOTP}
                      disabled={isLoading || !formData.employeeId}
                      sx={{
                        background: otpGenerated
                          ? 'linear-gradient(135deg, #059669 0%, #10b981 100%)'
                          : 'linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%)',
                        borderRadius: 3,
                        py: 2,
                        fontSize: '16px',
                        fontWeight: 700,
                        textTransform: 'none',
                        boxShadow: otpGenerated
                          ? '0 6px 24px rgba(5,150,105,0.4)'
                          : '0 6px 24px rgba(30,58,138,0.4)',
                        '&:hover': {
                          background: otpGenerated
                            ? 'linear-gradient(135deg, #047857 0%, #059669 100%)'
                            : 'linear-gradient(135deg, #1e40af 0%, #2563eb 100%)',
                          transform: 'translateY(-2px)',
                          boxShadow: otpGenerated
                            ? '0 8px 32px rgba(5,150,105,0.5)'
                            : '0 8px 32px rgba(30,58,138,0.5)'
                        },
                        '&:disabled': {
                          background: '#94a3b8',
                          color: 'white'
                        },
                        transition: 'all 0.3s ease'
                      }}
                    >
                      <Phone sx={{ mr: 1 }} />
                      {isLoading ? 'Generating...' : otpGenerated ? '‚úÖ OTP Sent' : 'Generate OTP'}
                    </Button>

                    <TextField
                      label="Enter OTP"
                      value={formData.otp}
                      onChange={handleInputChange('otp')}
                      error={!!errors.otp}
                      helperText={errors.otp}
                      fullWidth
                      disabled={!otpGenerated}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <Phone sx={{ color: '#1e3a8a' }} />
                          </InputAdornment>
                        )
                      }}
                      sx={{
                        '& .MuiOutlinedInput-root': {
                          borderRadius: 3,
                          background: otpGenerated ? 'rgba(255,255,255,0.9)' : 'rgba(0,0,0,0.05)',
                          '&:hover fieldset': {
                            borderColor: '#3b82f6',
                            borderWidth: 2
                          },
                          '&.Mui-focused fieldset': {
                            borderColor: '#1e3a8a',
                            borderWidth: 2
                          }
                        },
                        '& .MuiInputLabel-root': {
                          fontWeight: 600
                        }
                      }}
                    />

                    <Box sx={{ display: 'flex', alignItems: 'flex-start', gap: 2 }}>
                      <TextField
                        label="Enter CAPTCHA"
                        value={formData.captcha}
                        onChange={handleInputChange('captcha')}
                        error={!!errors.captcha}
                        helperText={errors.captcha}
                        fullWidth
                        InputProps={{
                          startAdornment: (
                            <InputAdornment position="start">
                              <Security sx={{ color: '#1e3a8a' }} />
                            </InputAdornment>
                          )
                        }}
                        sx={{
                          '& .MuiOutlinedInput-root': {
                            borderRadius: 3,
                            background: 'rgba(255,255,255,0.9)',
                            '&:hover fieldset': {
                              borderColor: '#3b82f6',
                              borderWidth: 2
                            },
                            '&.Mui-focused fieldset': {
                              borderColor: '#1e3a8a',
                              borderWidth: 2
                            }
                          },
                          '& .MuiInputLabel-root': {
                            fontWeight: 600
                          }
                        }}
                      />

                      <Paper elevation={4} sx={{
                        border: '3px solid #e5e7eb',
                        p: 2.5,
                        background: 'linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%)',
                        minWidth: '140px',
                        height: '56px',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        fontFamily: 'monospace',
                        fontSize: '22px',
                        fontWeight: 'bold',
                        color: '#1e3a8a',
                        borderRadius: 3,
                        letterSpacing: '3px',
                        textShadow: '1px 1px 2px rgba(0,0,0,0.1)'
                      }}>
                        {captchaText}
                      </Paper>

                      <Tooltip title="Generate new CAPTCHA">
                        <IconButton
                          onClick={generateNewCaptcha}
                          sx={{
                            mt: 0.5,
                            color: '#1e3a8a',
                            background: 'rgba(30,58,138,0.1)',
                            borderRadius: 3,
                            width: 56,
                            height: 56,
                            '&:hover': {
                              background: 'rgba(30,58,138,0.2)',
                              transform: 'rotate(180deg)'
                            },
                            transition: 'all 0.4s ease'
                          }}
                        >
                          <Refresh />
                        </IconButton>
                      </Tooltip>
                    </Box>

                    <Button
                      variant="contained"
                      onClick={handleLogin}
                      disabled={isLocked || isLoading}
                      sx={{
                        background: isLocked
                          ? '#64748b'
                          : isLoading
                            ? '#94a3b8'
                            : 'linear-gradient(135deg, #059669 0%, #10b981 100%)',
                        borderRadius: 3,
                        py: 2.5,
                        fontSize: '20px',
                        fontWeight: 800,
                        textTransform: 'none',
                        boxShadow: isLocked || isLoading ? 'none' : '0 8px 32px rgba(5,150,105,0.4)',
                        '&:hover': {
                          background: isLocked
                            ? '#64748b'
                            : isLoading
                              ? '#94a3b8'
                              : 'linear-gradient(135deg, #047857 0%, #059669 100%)',
                          transform: isLocked || isLoading ? 'none' : 'translateY(-3px)',
                          boxShadow: isLocked || isLoading ? 'none' : '0 12px 40px rgba(5,150,105,0.6)'
                        },
                        transition: 'all 0.4s ease'
                      }}
                    >
                      {isLoading ? (
                        <>
                          <LinearProgress sx={{ width: 100, mr: 2 }} />
                          Authenticating...
                        </>
                      ) : isLocked ? (
                        'üîí Account Locked'
                      ) : (
                        'üöÄ Login to Dashboard'
                      )}
                    </Button>

                    <Button
                      onClick={handleForgotPasswordClick}
                      sx={{
                        color: '#dc2626',
                        textTransform: 'none',
                        fontSize: '18px',
                        fontWeight: 600,
                        borderRadius: 3,
                        py: 2,
                        '&:hover': {
                          background: 'rgba(220,38,38,0.1)',
                          transform: 'translateY(-2px)'
                        },
                        transition: 'all 0.3s ease'
                      }}
                    >
                      üîë Forgot Password ?
                    </Button>
                  </Box>
                </TabPanel>
              </CardContent>
            </Card>
          </Box>
        </Fade>
      </Container>
    </Box>
  );
};

export default LoginPage;